<template>
  <div class="chat">
    <div class="chat__window">
      <ChatHeader :src="'user1'" :name="'Александр'" :status="'Онлайн'" />
      <ChatMain :sendId="2" :message="message" />
      <ChatFooter :sendId="2" @newMess="newMess" />
    </div>
    <div class="chat__window">
      <ChatHeader :src="'user2'" :name="'Евгений'" :status="'Онлайн'" />
      <ChatMain :sendId="1" :message="message" />
      <ChatFooter :sendId="1" @newMess="newMess" />
    </div>
  </div>
</template>

<script>
import ChatHeader from "./components/ChatHeader.vue";
import ChatMain from "./components/ChatMain.vue";
import ChatFooter from "./components/ChatFooter.vue";

export default {
  name: "App",
  components: {
    ChatHeader,
    ChatMain,
    ChatFooter,
  },
  data() {
    return {
      message: [],
    };
  },
  created() {
    const messStorage = localStorage.message
      ? JSON.parse(localStorage.message)
      : [];
    this.message = messStorage;
  },
  methods: {
    newMess(mess) {
      this.message.push(mess);
      localStorage.message = JSON.stringify(this.message);
    },
  },
};
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>
