<template>
  <footer>
    <input
      rows="1"
      placeholder="Написать сообщение..."
      class="chat__footer-message"
      v-model="message"
    />
    <button
      class="chat__footer-btn"
      @click="modal = true"
      v-if="message.length == 0"
    >
      <img src="../assets/imgs/photo.svg" alt="" />
    </button>
    <button class="chat__footer-btn" @click="sendMessage" v-else>
      <img src="../assets/imgs/send.svg" alt="" />
    </button>
    <Transition>
      <div class="modal" v-if="modal" @click="closeModal">
        <div class="modal__body" @click.stop.prevent>
          <div class="modal__title">Отправить картинку</div>
          <div class="modal__from">
            <label>
              <span>URL</span>
              <input v-model="photo" placeholder="URL" />
            </label>
            <label>
              <span>Комментарий</span>
              <textarea v-model="comment" placeholder="Комментарий"></textarea>
            </label>
          </div>
          <div class="modal__btns">
            <button class="modal-btn cancel" @click="closeModal">Отмена</button>
            <button class="modal-btn send" @click="sendPhoto">ОТПРАВИТЬ</button>
          </div>
        </div>
      </div>
    </Transition>
  </footer>
</template>

<script>
export default {
  name: "ChatFooter",
  data() {
    return {
      message: "",
      photo: "",
      comment: "",
      modal: false,
    };
  },
  props: {
    sendId: Number,
  },
  methods: {
    closeModal() {
      this.modal = false;
      (this.photo = ""), (this.comment = "");
    },

    sendMessage() {
      const sendTime = `${new Date().getHours()}:${new Date().getMinutes()}`;
      if (this.message.length > 0) {
        const mess = {};
        mess.body = this.message.trim();
        mess.sendId = this.sendId;
        mess.time = sendTime;
        this.$emit("newMess", mess);
        this.message = "";
      }
    },

    sendPhoto() {
      const sendTime = `${new Date().getHours()}:${new Date().getMinutes()}`;
      if (this.photo.length > 0) {
        const mess = {};
        mess.body = this.comment.trim();
        mess.photo = this.photo.trim();
        mess.sendId = this.sendId;
        mess.time = sendTime;
        this.$emit("newMess", mess);
        this.comment = this.photo = "";
        this.modal = false;
        this.photo = "";
        this.comment = "";
      }
    },
  },
};
</script>

<style>
footer {
  background: #323232;
  display: flex;
  align-items: center;
  padding: 16px 20px;
  width: 100%;
  justify-content: space-between;
}

.chat__footer-message {
  color: #fff;
  font-family: "Roboto";
  font-size: 16px;
  font-weight: 400;
}

.chat__footer-btn {
  width: 24px;
  height: 24px;
  background: transparent;
  cursor: pointer;
}

.chat__footer-btn:focus {
  background: rgba(255, 255, 255, 0.3);
}

input {
  width: 80%;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(0, 0, 0, 0.3);
}

.modal__body {
  border-radius: 28px;
  background: #f2f7ff;
  width: 312px;
  padding: 24px;
  text-align: left;
}

.modal__title {
  color: #1c1b1f;
  font-size: 24px;
  font-weight: 400;
  line-height: 133%;
}

.modal__from {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-top: 16px;
}

.modal__from label {
  position: relative;
}

.modal__from span {
  color: #000;
  font-size: 12px;
  font-weight: 400;
  line-height: 133%;
  letter-spacing: 0.4px;
  position: absolute;
  top: 8px;
  left: 16px;
}

.modal__from input,
.modal__from textarea {
  color: #49454f;
  font-size: 16px;
  font-weight: 400;
  line-height: 150%;
  letter-spacing: 0.5px;
  font-family: "Roboto";
  border-radius: 4px 4px 0px 0px;
  background: #e2edff;
  border-bottom: 1px solid black;
  width: 100%;
  height: 57px;
  display: flex;
  padding: 23px 16px 9px;
}

.modal__btns {
  display: flex;
  align-items: center;
  justify-content: end;
  gap: 5px;
  margin-top: 24px;
}

.modal-btn {
  padding: 10px 12px;
  text-align: center;
  font-size: 14px;
  font-weight: 500;
  line-height: 143%;
  letter-spacing: 0.1px;
  text-transform: uppercase;
  cursor: pointer;
}

.cancel {
  color: #cf1b1b;
}
.cancel:hover {
  background: #ffe6e6;
}

.send {
  color: #6750a4;
}
.send:hover {
  background: #eae3ff;
}
</style>
