<template>
  <main>
    <div class="main__item" v-for="(mess, index) in message" :key="index">
      <div
        class="main__item-body"
        :class="{
          send: mess.sendId == sendId,
          get: mess.sendId != sendId,
          photo: mess.photo?.length > 0 && mess.body.length == 0,
          'photo-text': mess.photo?.length > 0 && mess.body.length > 0,
        }"
      >
        <span class="main__item-time">{{ mess.time }}</span>
        <div class="main__item-text">
          <img :src="mess.photo" v-if="mess.photo?.length > 0" alt="" />
          <p>{{ mess.body }}</p>
        </div>
      </div>
    </div>
  </main>
</template>

<script>
export default {
  name: "ChatMain",
  props: {
    message: Array,
    sendId: Number,
  },
};
</script>

<style>
main {
  height: 632px;
  overflow: auto;
}

.main__item {
  display: flex;
  flex-direction: column;
  row-gap: 10px;
  padding: 10px;
}

.main__item-body {
  display: flex;
  align-items: center;
  padding: 0 10px;
}

.main__item-text {
  max-width: 80%;
  word-wrap: break-word;
  display: flex;
  padding: 3px;
  flex-direction: column;
  text-align: left;
  padding: 5px 10px;
}

.main__item-text p {
  color: #000;
  font-size: 16px;
  font-weight: 400;
}

.main__item-body.send {
  justify-content: end;
}

.main__item-body.send .main__item-text {
  border-radius: 15px 15px 0px 15px;
  background: #c4bfff;
  margin-left: 5px;
}

.main__item-text img {
  width: 100%;
  border-radius: 13px;
}

.main__item-body.get {
  justify-content: start;
  flex-direction: row-reverse;
}

.main__item-body.get .main__item-text {
  background: #d0dcff;
  border-radius: 15px 15px 15px 0px;
  margin-right: 5px;
}

.main__item-body.photo .main__item-text {
  border-radius: 15px;
  padding: 2px;
}

.main__item-body.photo-text .main__item-text {
  border-radius: 15px 15px 0 15px;
  padding: 2px 2px 6px 2px;
}

.main__item-body.photo-text p {
  padding: 5px 5px 0;
}

.main__item-body .main__item-time {
  padding-right: 5px;
  color: #323232;
  font-size: 14px;
  font-weight: 400;
}
</style>
